
import { AppDispatch, RootState } from "@/redux/globalStore";
import { openResponsive } from "@/redux/menuSlice";
import { navigationList } from "@/utils/globalList";
import Link from "next/link";
import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";

export default function Navbar() {
  const dispatch = useDispatch<AppDispatch>();
  const { responsive } = useSelector((state: RootState) => state.menus);

  const handleOpenResponsive = () => {
    dispatch(openResponsive(!responsive));
  };

  // trạng thái mở dropdown mobile cho từng mục
  const [openDropdown, setOpenDropdown] = useState<string | null>(null);

  const handleToggleDropdown = (id: string) => {
    setOpenDropdown((prev) => (prev === id ? null : id));
  };

  return (
    <div>
      {/* desktop navigation */}
      <nav className="justify-center hidden md:flex">
        <ul className="flex gap-5">
          {navigationList.map((nav) => (
            <li key={nav.id} className="group relative">
              <Link href={nav.url} className="text-black font-semibold">
                {nav.content}
              </Link>
              {nav.dropdownMenu?.length > 0 && (
                <ul className="absolute bg-white flex-col gap-2 dropdown-menu hidden group-hover:flex shadow p-2 rounded">
                  {nav.dropdownMenu.map((drop) => (
                    <li key={drop.id}>
                      <Link href={drop.url} className="font-semibold">
                        {drop.content}
                      </Link>
                    </li>
                  ))}
                </ul>
              )}
            </li>
          ))}
        </ul>
      </nav>

      {/* mobile navigation */}
      {responsive && (
        <nav className="px-2 md:hidden">
          <ul className="flex flex-col gap-5">
            {navigationList.map((nav) => (
              <li key={nav.id} className="relative">
                <button
                  onClick={() =>
                    nav.dropdownMenu?.length
                      ? handleToggleDropdown(nav.id)
                      : handleOpenResponsive()
                  }
                  className="text-left w-full text-black font-semibold"
                >
                  {nav.content}
                </button>

                {/* dropdown ở mobile dùng state để toggle */}
                {openDropdown === nav.id && nav.dropdownMenu?.length > 0 && (
                  <ul className="flex flex-col bg-white shadow-lg px-2 mt-1 rounded">
                    {nav.dropdownMenu.map((drop) => (
                      <li key={drop.id}>
                        <Link
                          href={drop.url}
                          className="font-semibold block py-1"
                          onClick={handleOpenResponsive}
                        >
                          {drop.content}
                        </Link>
                      </li>
                    ))}
                  </ul>
                )}
              </li>
            ))}

            {/* sign in / logout button */}
            <li>
              <button className="bg-cyan-500 py-1 w-full rounded font-semibold text-white">
                Sign in
              </button>
            </li>
            <li>
              <button className="bg-gray-400 py-1 w-full font-semibold rounded">
                Logout
              </button>
            </li>
          </ul>
        </nav>
      )}
    </div>
  );
}
